import React from 'react';

import Avatar from 'components/Avatar';

import { findMemberInfo } from 'functions/member';

import './index.scss';

function NameCard({ name }: { name: string }) {
  const { nickname, description } = findMemberInfo(name);

  return (
    <div className='name-card'>
      <Avatar name={name} size='md' />
      <div className='name-card__text-area'>
        <div className='name-card__title'>{nickname}</div>
        <div className='name-card__description'>{description}</div>
      </div>
    </div>
  );
}

export default NameCard;
